.chat {
  flex: 0.65;
  display: flex;
  flex-direction: column;

}

.chat-header {
  display: flex;
  padding: 30px;
  align-items: center;
  border-bottom: 2px solid lightgray;
}

.chat-header-info {
  flex: 1;
  padding-left: 20px;
}

.chat-header-info>h3 {
  margin-bottom: 3px;
  min-width: 100px;


}

.chat-header-right {
  display: flex;
  justify-content: space-between;
  min-width: 100px;

}

.chat-body {
  flex: 1;
  background-color: grey;
  /* background-image: url(https: //encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcT9_8St9dM0qHBf15E6imcl6nMFwxOTCV0Uqw&usqp=CAU);} */
}

.chat-message {
  position: relative;
  font-size: 16px;
  padding: 10px;
  border-radius: 10px;
  width: fit-content;
  background-color: white;
  margin: 30px 30px 30px 30px;



}

.chat-name {
  position: absolute;
  top: -15px;
  font-size: x-small;
  font-weight: 800;

}

.chat-time {
  margin-left: 10px;
  font-size: xx-small;
}

.chat-reciver {
  margin-left: auto;
  background-color: #6AC47E;

}

.chat-footer {
  display: flex;
  justify-content: space-between;
  height: 62px;
  border-top: 1px solid lightgray;
}

.chat-footer>form {
  flex: 1;
  display: flex;
 
}

.chat-footer>form>input {
  flex: 10;
  border-radius: 30px;
  padding: 10px;
  border: none;
  /* width: 100px;; */

}

.chat-footer>form>button {
  display: none;
}